<template>
    <el-container>
        <el-header>
            <container-top></container-top>
        </el-header>
        <el-container>
            <nav-header></nav-header>
            <el-main>
                <el-container class="container-main-div">
                    <div class="container-div">
                        <div class="container-main">
                            <div class='containers' v-loading="runloading" style="display: flex;" element-loading-text="" element-loading-spinner="el-icon-loading">
                                <div class="leftHeight">
                                    <el-input placeholder="" v-model="filterText" class="filterInput" prefix-icon="el-icon-search"></el-input>
                                    <el-tree
                                      class="filter-tree"
                                      :data="data"
                                      :props="defaultProps"
                                      :default-expand-all="true"
                                      :filter-node-method="filterNode"
                                      :highlight-current="true"
                                      @node-click="handleNodeClick"
                                      @node-expand="nodeExpand"
                                      ref="tree">
                                        <span class="custom-tree-node onePointTree" slot-scope="{ node, data }" style="position: relative;">
                                            <span style="position: relative;">
                                                <p :data-order="data.order" :class="data.class" :data-oid="data.oid" :data-operatorpath="data.operatorpath">{{node.label}}</p>
                                            </span>
                                        </span>
                                    </el-tree>
                                </div>
                                <div class="rightHeight">
                                    <div class="operheight">
                                        <div class="operleft" style="flex: auto;display: flex;"></div>
                                        <div class="runDiv" style="width: 300px;display: flex;">
                                            <p style="color: #fff;" @click=""><i class="el-icon-warning"></i></p>
                                            <el-button type="primary" @click="runDataWork" size="small" style="background: #0E50E1;border:0px"></el-button>
                                        </div>
                                    </div>
                                    <div class="Flowheight">
                                        <div style="height: 720px;overflow-y: auto;flex: auto;" class="flowscroll">
                                            <div class="mainFlowAataWork"></div>
                                        </div>
                                        <div class="rightFlow">
                                            <div>
                                                <div class="operateDiv">
                                                    <p style="color: #000;text-align: center;font-weight: bold;margin-bottom: 10px;"></p>
                                                    <el-form label-width="100px">
                                                        <el-form-item label="">
                                                            <el-input class="inputAllStyle" v-model="sourceName"></el-input>
                                                        </el-form-item>
                                                    </el-form>
                                                </div>
                                            </div>
                                            <div v-show="clickIndex=='sequential'">
                                                <div class="operateDiv">
                                                    <p style="color: #000;text-align: center;font-weight: bold;"></p>
                                                    <el-form label-width="80px">
                                                        <el-form-item label="">
                                                            <el-date-picker v-model="startDate" type="date" placeholder="" style="width: 100%;"></el-date-picker>
                                                        </el-form-item>
                                                        <el-form-item label="">
                                                            <el-date-picker v-model="endDate" type="date" placeholder="" style="width: 100%;"></el-date-picker>
                                                        </el-form-item>
                                                    </el-form>
                                                </div>
                                            </div>
                                            <div v-show="clickIndex=='rule'">
                                                <div class="operateDiv">
                                                    <p style="color: #000;text-align: center;font-weight: bold;"></p>
                                                    <el-button type="primary" @click="handleAdd" size="small" style="margin-top: 10px;"></el-button>
                                                </div>
                                            </div>
                                            <div v-show="clickIndex=='nodeInfo'">
                                                <div class="operateDiv">
                                                    <p style="color: #000;text-align: center;font-weight: bold;">{{nodeName}}</p>
                                                    <div style="margin: 15px 0;"></div>
                                                    <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                                                        <el-checkbox v-for="city in cities" :label="city" :key="city">{{city.split('/#/')[1]}}</el-checkbox>
                                                    </el-checkbox-group>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </el-container>
            </el-main>
        </el-container>
    </el-container>
</template>
