                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    var data=response.data.nodes;
                    that.data=[{id: 1,label: '',class:'nodrag',children: []},{id: 100,label: 'Y',class:'nodrag',children: [{id: 101,class:'thisb sequential',label: ''},{id: 102,class:'thisb rule',label: ''}]},{id: 200,label: '',class:'nodrag',children: [{id: 201,class:'thisc alignment',label: ''},{id: 202,class:'thisc examine',label: ''}]}]
                    if(data.length==0){
                        that.data=[{id: 1,label: '',class:'nodrag',children: []},{id: 100,label: 'Y',class:'nodrag',children: [{id: 101,class:'thisb sequential',label: ''},{id: 102,class:'thisb rule',label: ''}]},{id: 200,label: '',class:'nodrag',children: [{id: 201,class:'thisc alignment',label: ''},{id: 202,class:'thisc examine',label: ''}]}]
                    }
                    else {
                        $.each(data,function(index, el) {
                            that.data[0].children.push({
                                id: index+2,
                                class: 'thisa define'+(index+2),
                                label: el.nodeName,
                            })
                        });
                    }
                    setTimeout(function(){
                        that.flowEdit()
                    },600)
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },
