                    $.each(response.data.attributes,function(index, el) {
                        if(el.provider==nodeName){
                            that.cities.push(el.cnName?el.provider+'/#/'+el.cnName:el.provider+'/#/'+el.attrName)
                        }
                    });
                }
                else {
                    that.$message.error(response.data.message);
                }
