            flowEdit: function() {
                var that=this
                $(".leftHeight .thisa,.leftHeight .thisb,.leftHeight .thisc").draggable({
                    helper:'clone',
                    scpoe:'dragflag'
                })
                $(".mainFlowAataWork").droppable({
                    accept:'.leftHeight .thisa,.leftHeight .thisb,.leftHeight .thisc',
                    addClasses: false,
                    scpoe:'dragflag',
                    drop: function (event, ui) {
                        var text = ui.draggable[0].innerText;
                        var listclass=ui.draggable[0].classList[0]
                        var order=ui.draggable[0].dataset.order;
                        var oid=ui.draggable[0].dataset.oid;
                        var operatorpath=ui.draggable[0].dataset.operatorpath;
                        var clickid=ui.draggable[0].classList[1]
                        var scrolltop = $(".mainFlowAataWork").offset().top
                        var left=ui.offset.left-500+'px';
                        var top=ui.offset.top-scrolltop+'px';
                        jsPlumb.importDefaults({ConnectionsDetachable: false})
                        var common = {
                            connector:["Bezier", {curviness: 63}],
                            endpoint: ['Dot',{ radius:6}],
                            paintStyle: {
                                fill: "transparent",
                                radius: 4,
                            },
                            hoverPaintStyle: {
                                outlineStroke: 'lightblue'
                            },
                            connectorStyle: {
                                outlineStroke: '#0065ff',
                                strokeWidth: 1
                            },
                            connectorHoverStyle: {
                                strokeWidth: 2
                            },
                            connectorOverlays: [["Arrow", { width: 20, length: 20, location: 1,cssClass:"myArrow"}],[ "Label", { label:"",cssClass:"myClose"}]],
                            maxConnections:1
                        }
                        var commonMax = {
                            connector:["Bezier", {curviness: 63}],
                            endpoint: ['Dot',{ radius:6}],
                            paintStyle: {
                                fill: "transparent",
                                radius: 4,
                            },
                            hoverPaintStyle: {
                                outlineStroke: 'lightblue'
                            },
                            connectorStyle: {
                                outlineStroke: '#0065ff',
                                strokeWidth: 1
                            },
                            connectorHoverStyle: {
                                strokeWidth: 2
                            },
                            connectorOverlays: [["Arrow", { width: 20, length: 20, location: 1,cssClass:"myArrow"}],[ "Label", { label:"",cssClass:"myClose"}]],
                            maxConnections:20
                        }
                        if(listclass=='thisa'){
                            that.text=text
                            that.clickid=clickid
                            that.order=order
                            that.top=top
                            that.left=left
                            if($(".oneStep"+clickid).length==0){
                                var randomPassword=that.randomPassword()
                                that.nodeName=text
                                that.checkId=randomPassword
                                that.clickIndex="nodeInfo"
                                that.attributeReq(text)
                                var html='<div data-clickid="'+clickid+'" data-order="'+order+'" class="oneStep oneStep2 oneStep'+clickid+'" id="'+clickid+'" data-id="'+clickid+randomPassword+'" data-name="'+text+'" style="left:'+left+';top:'+top+';"><div class="dragContent"><i class="el-icon-delete deleteThis2"></i><i class="el-icon-user iconType2"></i><i class="el-icon-warning-outline iconWarning icon_'+clickid+'"></i>'+text+'</div></div>'
                                $(html).appendTo($(".mainFlowAataWork"))
                                $(".oneStep").removeClass('hover')
                                $("#"+clickid).addClass('hover');
                                jsPlumb.addEndpoint(clickid,{anchors: "BottomCenter",isSource:true,cssClass:"myPoint",uuid:clickid+'T'},common);
                                jsPlumb.draggable(clickid);
                                $("#"+clickid).draggable({ containment: ".mainFlowAataWork",grid: [5, 5]});
                            }
                            else {
                                that.$message.error("");
                            }
                        }
                        if(listclass=='thisb'){
                            that.text=text
                            that.clickid=clickid
                            that.order=order
                            that.top=top
                            that.left=left
                            if($(".oneStep"+clickid).length==0){
                                that.clickIndex=clickid
                                var html='<div data-clickid="'+clickid+'" data-order="'+order+'" class="oneStep oneStep3 oneStep'+clickid+'" id="'+clickid+'" data-name="'+text+'" style="left:'+left+';top:'+top+';"><div class="dragContent"><i class="el-icon-delete deleteThis3"></i><i class="el-icon-s-data iconType3"></i><i class="el-icon-warning-outline iconWarning icon_'+clickid+'"></i>'+text+'</div></div>'
                                $(html).appendTo($(".mainFlowAataWork"))
                                $(".oneStep").removeClass('hover')
                                $("#"+clickid).addClass('hover');
                                jsPlumb.addEndpoint(clickid,{anchors: "BottomCenter",isSource:true,cssClass:"myPoint",uuid:clickid+'T'},common);
                                jsPlumb.addEndpoint(clickid,{anchors: "TopCenter",isTarget:true,cssClass:"myPoint",uuid:clickid+'T'},commonMax);
                                jsPlumb.draggable(clickid);
                                $("#"+clickid).draggable({ containment: ".mainFlowAataWork",grid: [5, 5]});
                            }
                            else {
                                that.$message.error("");
                            }
                        }
                        else if(listclass=='thisc'){
                            that.text=text
                            that.clickid=clickid
                            that.order=order
                            that.top=top
                            that.left=left
                            if(clickid=='alignment'){
                                var len=$(".mainFlowAataWork .oneStepalign").length;
                                if(len==0){
                                    that.clickIndex=clickid
                                    var html='<div data-clickid="'+clickid+'" class="oneStep oneStep4 oneStepalign" id="'+clickid+'" data-name="'+text+'" style="left:'+left+';top:'+top+';"><div class="dragContent"><i class="el-icon-delete deleteThis4"></i><i class="el-icon-s-grid iconType4"></i><i class="el-icon-warning-outline iconWarning icon_'+clickid+'"></i>'+text+'</div></div>'
                                    $(html).appendTo($(".mainFlowAataWork"))
                                    $(".oneStep").removeClass('hover')
                                    $("#"+clickid).addClass('hover');
                                    jsPlumb.addEndpoint(clickid,{anchors: "TopCenter",isTarget:true,cssClass:"myPoint",uuid:clickid+'T'},commonMax);
                                    jsPlumb.draggable(clickid);
                                    $("#"+clickid).draggable({ containment: ".mainFlowAataWork",grid: [5, 5]});
                                }
                                else{
                                    that.$message.error("");
                                }
                            }
                        }
                        $(".deleteThis2,.deleteThis3,.deleteThis4").on("click",function(val){
                            jsPlumb.remove($(this).parent().parent().attr("data-clickid"))
                            jsPlumb.removeAllEndpoints($(this).parent().parent().attr("data-clickid"))
                            $(this).parent().parent().remove()
                            that.clickIndex="normal"
                        })

                        $(".oneStep2").on("click",function(val){
                            that.clickIndex="nodeInfo"
                            that.nodeName=$(this).attr("data-name")
                            that.checkId=$(this).attr("data-id")
                            that.attributeReq($(this).attr("data-name"))
                        })
                        $(".oneStep3,.oneStep4").on("click",function(val){
                            that.clickIndex=$(this).attr("data-clickid")
                        })
                        jsPlumb.bind("connection", function(info, originalEvent) {
                            that.clickIndex="normal"
                        });
                        jsPlumb.bind('click', function (conn, originalEvent) {
                            jsPlumb.deleteConnection(conn)
                            that.clickIndex="normal"
                        })
                    }
                })
            },
        // end 


        // 
        runDataWork:function(){
            var that=this;
            var list = jsPlumb.getAllConnections();
            var preArr=[]
            var divCount=$(".mainFlowAataWork").find(".oneStep").length
            var providerCount=$(".mainFlowAataWork").find(".oneStep2").length
            var progressCount=$(".mainFlowAataWork").find(".oneStep3").length
            var resultCount=$(".mainFlowAataWork").find(".oneStep4").length
            var justOne=[]
            var json2=""
            var json1='{"point":['
            $.each(list,function(index, el) {
                preArr.push(el.targetId);
                json2+='{"sourceId": "'+el.sourceId+'","targetId": "'+el.targetId+'"},'
            });
            json2=json2.substring(0,json2.length-1)
            json2='"connect":['+json2+']'
            if(providerCount==0){
                that.$message.error("")
            }
            else if(resultCount==0){
                that.$message.error("")
            }
            else{
                if(divCount>jsPlumb.getAllConnections().length+1){
                    that.$message.error("")
                }
                else if(that.sourceName==""){
                    that.$message.error("")
                }
                else{
                    // 
                    var okCount=0;
                    var okCount2=0;
                    var dict='[';
                    for(var i=0;i<divCount;i++){
                        // 
                        if($(".mainFlowAataWork").find(".oneStep").eq(i).attr("data-clickid").indexOf("define")>-1){
                            var nowtarget=$(".mainFlowAataWork").find(".oneStep").eq(i).attr("data-clickid")
                            console.log(that.checkedCities)
                            var attributesArr=[]
                            $.each(that.checkedCities,function(index, el) {
                                if($(".mainFlowAataWork").find(".oneStep").eq(i).attr("data-name")==el.split("/#/")[0]){
                                    attributesArr.push('"'+el.split("/#/")[1]+'"')
                                }
                            });
                            dict+='{"provider":"'+$(".mainFlowAataWork").find(".oneStep").eq(i).attr("data-name")+'","attributes":['+attributesArr+']},';
                            json1+='{"id": "'+$(".mainFlowAataWork").find(".oneStep").eq(i).attr("data-clickid")+'","name": "'+$(".mainFlowAataWork").find(".oneStep").eq(i).attr("data-name")+'","param": {"attributes":['+attributesArr+']},"type": "1","clickid":"'+$(".mainFlowAataWork").find(".oneStep").eq(i).attr("data-clickid")+'","top": "'+$(".mainFlowAataWork").find("#"+$(".mainFlowAataWork").find(".oneStep").eq(i).attr("data-clickid")).css('top')+'","left": "'+$(".mainFlowAataWork").find("#"+$(".mainFlowAataWork").find(".oneStep").eq(i).attr("data-clickid")).css('left')+'"},'
                        }
                        // 
                        else if($(".mainFlowAataWork").find(".oneStep").eq(i).attr("data-clickid")=="alignment"){
                            json1+='{"id": "alignment","name": "","param": {},"type": "3","clickid":"alignment","top": "'+$(".mainFlowAataWork").find("#"+$(".mainFlowAataWork").find(".oneStep").eq(i).attr("data-clickid")).css('top')+'","left": "'+$(".mainFlowAataWork").find("#"+$(".mainFlowAataWork").find(".oneStep").eq(i).attr("data-clickid")).css('left')+'"},'
                        }
                    }
                    dict=dict.substring(0,dict.length-1)
                    dict+=']'
                    json1=json1.substring(0,json1.length-1)
                    json1+='],'+json2
                    json1+='}'
                    console.log(json1)
                    console.log(dict)
                    if(okCount>=0&&okCount2>=0){
                        that.runloading=true
                        that.axios.post('/datasetGen',this.qs.stringify({
                            "scene":decodeURI(location.href.split("?sceneName=")[1].split("&")[0]),
                            "datasetName":that.sourceName,
                            "dict":dict,
                            "config":json1,
                            "operator":that.username,
                            "token":that.token
                        }),
                            {
                                headers:{
                                    'Content-Type':'application/x-www-form-urlencoded',
                                }
                            },
                        {
                            emulateJSON:true
                        })
                        .then(function(response){
                            if(response.data.message.state){
                                Cookies.set("isReload2",false)
                                that.runloading=false
                                that.$message({
                                    message: '',
                                    type: 'success'
                                });
                                setTimeout(function(){
                                    that.$router.push('/dataManage');
                                },1000)
                            }
                            else{
                                that.runloading=false
                                that.$message.error(response.data.message.message);
                            }
                        },function(error){
                            that.$message.error(error.response.data.message.message);
                            if(error.response.status==401){
                                that.$router.push('/');
                                Cookies.set("v2tokens",1,{expires:-1});
                                Cookies.set("v2name",1,{expires:-1});
                                Cookies.set("v2role",1,{expires:-1});
                                Cookies.set("v2time",1,{expires:-1});
                            }
                        })
                    }
                    else{
                        //
                    }
                }
            }
        },


        // 
            nodeExpand:function(){
                var len=$(".thisb").length;
                for(var i=0;i<len;i++){
                    $(".thisb").eq(i).draggable({
                        helper:'clone',
                        scpoe:'dragflag'
                    })
                }
            },
            randomPassword:function(){
                var seed = new Array('A','B','C','D','E','F','G','H','I','J','K','L','M','N','P','Q','R','S','T','U','V','W','X','Y','Z','a','b','c','d','e','f','g','h','i','j','k','m','n','p','Q','r','s','t','u','v','w','x','y','z','1','2','3','4','5','6','7','8','9'
                );//
                var seedlength = seed.length;//
                var createPassword = '';
                for (var i=0;i<6;i++) {
                    var j = Math.floor(Math.random()*seedlength);
                    createPassword += seed[j];
                }
                return createPassword;
            },
            handleNodeClick:function(data){

            },
            filterNode(value, data) {
                if (!value) return true;
                return data.label.indexOf(value) !== -1;
            },
            findDimensions:function(){
                if(window.innerWidth)
                this.winWidth = window.innerWidth;
                else if ((document.body) && (document.body.clientWidth))
                this.winWidth = document.body.clientWidth;
                //
                if(window.innerHeight)
                this.winHeight = window.innerHeight;
                else if((document.body) && (document.body.clientHeight))
                this.winHeight = document.body.clientHeight;
                //Documentbody
                if(document.documentElement && document.documentElement.clientHeight && document.documentElement.clientWidth){
                    this.winHeight = document.documentElement.clientHeight;
                    this.winWidth = document.documentElement.clientWidth;
                }
                jsPlumb.repaintEverything()
                $(".Flowheight,.mainFlowAataWork").attr("style","height:"+(this.winHeight-250)+"px;display:flex;flex:auto;margin-top:50px")
                $(".mainFlowAataWork").attr("style","height:"+(this.winHeight*2)+"px;display:flex;flex:auto")
                $(".leftHeight,.rightHeight").attr("style","height:"+(this.winHeight-120)+"px;")
                $(".filter-tree").attr("style","height:"+(this.winHeight-250)+"px;overflow-x: hidden !important;")
            },
        // end 
    },
};
</script>



